export function validateEmail(email) {
	const re = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i
	const result = re.test(String(email).toLowerCase())
	if (result) { return { result: result, message: '' } }

	return { result: result, message: 'Invalid email format.' }
}

